body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(1deg, #ff5edf 0%, #04c8de 100%);
  background: linear-gradient(89deg, #ff5edf 0%, #04c8de 100%)
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  cursor: pointer;
}
.Tilt {
    background: -webkit-linear-gradient(1deg, #ff5edf 0%, #04c8de 100%);
    background: linear-gradient(89deg, #ff5edf 0%, #04c8de 100%);
}
.form {
    width: 700px;
    background: #36c;
background:
-webkit-linear-gradient(335deg, transparent 75%, rgba(255,255,255,.8) 75%) 0 0,
-webkit-linear-gradient(205deg, transparent 75%, rgba(255,255,255,.8) 75%) 0 0,
-webkit-linear-gradient(335deg, transparent 75%, rgba(255,255,255,.8) 75%) 7px -15px,
-webkit-linear-gradient(205deg, transparent 75%, rgba(255,255,255,.8) 75%) 7px -15px,
#36c;
background:
linear-gradient(115deg, transparent 75%, rgba(255,255,255,.8) 75%) 0 0,
linear-gradient(245deg, transparent 75%, rgba(255,255,255,.8) 75%) 0 0,
linear-gradient(115deg, transparent 75%, rgba(255,255,255,.8) 75%) 7px -15px,
linear-gradient(245deg, transparent 75%, rgba(255,255,255,.8) 75%) 7px -15px,
#36c;
background-size: 15px 30px;
}
.App {
  text-align: center;
}

.center {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}
.bounding-box {
    position: absolute;
    box-shadow: 0 0 0 3px #149df2 inset;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    cursor: pointer;
}
