}

.input-reset {
    -webkit-appearance: none;
       -moz-appearance: none;
}

.input-reset::-moz-focus-inner {
    border: 0;
    padding: 0;
}

.lh-copy {
    line-height: 1.5;
}

.link {
    text-decoration: none;
    -webkit-transition: color .15s ease-in;
    transition: color .15s ease-in;
}

.link:link, .link:visited {
    -webkit-transition: color .15s ease-in;
    transition: color .15s ease-in;
}

.link:hover {
    -webkit-transition: color .15s ease-in;
    transition: color .15s ease-in;
}

.link:active {
    -webkit-transition: color .15s ease-in;
    transition: color .15s ease-in;
}

.link:focus {
    -webkit-transition: color .15s ease-in;
    transition: color .15s ease-in;
    outline: 1px dotted currentColor;
}

